import router from './router'
import i18n from './lang'
import store from './store'
import anchor from './anchor'

router.beforeEach((to, from, next) => {
  /* title */
  if (to.meta.title) {
    document.title = to.meta.title
  }

  // next()

  let lang = to.path.split('/')[1]
  switch (lang) {
    case 'zhCN':
      i18n.locale = 'zhCN'
      store.dispatch('setLanguage', 'zhCN')
      break
    case 'zhTW':
      i18n.locale = 'zhTW'
      store.dispatch('setLanguage', 'zhTW')
      break
    case 'en':
      i18n.locale = 'en'
      store.dispatch('setLanguage', 'en')
      break
    case 'ja':
      i18n.locale = 'ja'
      store.dispatch('setLanguage', 'ja')
      break
    default: 
      break
  }
  let learn = to.path.split('/')[2]
  switch (learn) {
    case 'vpn':
      store.state.anchor.activeAnchor = anchor.vpn
      break
    default:
      break
  }

  next({params: { path: to.path }})
  
})