  padding-bottom: 3px;
}
.current {
  border-bottom: 3px solid #45b97c;
}
.arrow {
  display: inline-block;
  vertical-align: middle;
  margin-top: -1px;
  margin-left: 6px;
  /*margin-right: -14px;*/
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 5px solid #ccc;
}
.menu .nav-dropdown-container {
  flex: 1;
  justify-content: space-between;
  text-align: center;
  line-height: 50px;
  position: relative;
}
.nav-dropdown {
  width: 100%;
  position: absolute;
  /*top: calc(100% + 5px);*/
  top: 100%;
  right: 0;
  z-index: 3;
  background-color: #fff;
  opacity: 0;
  visibility: hidden;
  transition: all .5s;
}
.nav-dropdown li {
  line-height: 2.8em;
}
.nav-dropdown li:hover {
  color: #45b97c;
  cursor: pointer;
}
