import Vue from 'vue'
import VueI18n from 'vue-i18n'
import Cookies from 'js-cookie'

import enLocale from './en'
import zhCNLocale from './zh-CN'
import zhTWLocale from './zh-TW'
import jaLocale from './ja'
import elementEnLocale from 'element-ui/lib/locale/lang/en' // element-ui lang
import elementZhCNLocale from 'element-ui/lib/locale/lang/zh-CN'
import elementZhTWLocale from 'element-ui/lib/locale/lang/zh-TW'
import elementJaLocale from 'element-ui/lib/locale/lang/ja'

Vue.use(VueI18n)

const messages = {
  zhCN: Object.assign(zhCNLocale, elementZhCNLocale),
  zhTW: Object.assign(zhTWLocale, elementZhTWLocale),
  en: Object.assign(enLocale, elementEnLocale),
  ja: Object.assign(jaLocale, elementJaLocale)
  // ...enLocale, ...elementEnLocale
}

const i18n = new VueI18n({
  locale: Cookies.get('language') || 'zhCN',
  messages
})

export default i18n