<template>
  <div :class="['sidebar', {'isclose': sidebar.opened}]">
    <div class="sidebar-inner">
      <ul class="menu-root">
        <!-- <li><a class="sidebar-link" href="#shadowsocks">shadowsocks</a></li> -->
        <li class="nav-dropdown-container" v-for="item in menu" :key="item.title">
          <router-link :class="['nav-link', {'current': item.isCurrent}]" :to="item.hasItem ? '' : item.link">
            {{item.title}}
          </router-link>
          <span class="arrow" v-if="item.hasItem"></span>
          <ul :class="['nav-dropdown', {'show': item.isCurrent}]" v-if="item.hasItem">
            <li v-for="i in item.menuItem" :key="i.title">
              <router-link class="nav-link" :class="{'active': i.lang === langCurrent}" :to="i.link ? i.link : ''" @click.native="clickHandle(i)">{{i.title}}</router-link>
            </li>
          </ul>
        </li>
      </ul>
      <div class="menu-anchor-container" v-if="!anchor === false">
        <h3>{{anchor.title}}</h3>
        <ul class="menu-anchor">
          <li v-for="item in anchor.anchor" :key="item">
            <a class="nav-link" :href="'#' + item">{{item}}</a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import anchor from '@/anchor'

export default {
  created () {
    // 
    this.langCurrent = this.$store.getters.language
    //  navbar fixed
    // console.log(document.scrollTop)
    // let scrollTop = window.pageYoffset
  },
  computed: {
    ...mapGetters(['sidebar']),
    ...mapGetters(['anchor'])
  },
  data () {
    return {
      menu: [{
        title: this.$t('navbar.learn'), menuItem: [{ title: this.$t('route.vpn'), link: `/${this.$t('locale')}/vpn` }], hasItem: true, isCurrent: false
      }, {
        title: this.$t('navbar.language'), menuItem: [{ title: '', lang: 'zhCN', link: false }, { title: '', lang: 'zhTW', link: false }, { title: 'English', lang: 'en', link: false }, { title: '', lang: 'ja', link: false }], hasItem: true, isCurrent: false
      }, {
        title: this.$t('navbar.profile'), menuItem: [], hasItem: false, isCurrent: false, link: `/${this.$t('locale')}/profile`
      }],
      homeLang: '/' + this.$route.fullPath.split('/')[1]
    }
  },
  methods: {
    clickHandle (i) {

      if (i.link && i.link.indexOf('profile') === -1) {
        if (i.link.split('/').pop() === 'vpn') {
          this.$store.state.anchor.activeAnchor = anchor.vpn
        }
      }

      if (!i.link) {
         // switch 
        // switch (lang) {
        //   case '':
        //     this.$store.state.language = 'zhCN';
        //     this.$i18n.locale = 'zhCN'
        //     Cookies.set('language', 'zhCN');
        //     break
        //   case '':
        //     this.$store.state.language = 'zhTW';
        //     Cookies.set('language', 'zhTW');
        //     break
        //   case 'English':
        //     this.$store.state.language = 'en';
        //     this.$i18n.locale = 'en'
        //     Cookies.set('language', 'en');
        //     break
        //   case '':
        //     this.$store.state.language = 'ja';
        //     this.$i18n.locale = 'ja'
        //     Cookies.set('language', 'ja');
        //     break
        // }
        this.$i18n.locale = i.lang
        this.$store.dispatch('setLanguage', i.lang)
        // : 
        // 1
        // let NewPage = '.'
        // // push
        // this.$router.push(NewPage)
        // // 
        // this.$router.go(-1)

        // 2
        // router.resolve(location, current?, append?)
        // 32

        // qzsk.top/en/learn
        let pathArr = this.$route.fullPath.split('/')
        pathArr.splice(1, 1, i.lang)
        let path = pathArr.join('/')
        // console.log(path)
        this.$router.push(path)
      }
    }
  }
}
</script>

