<template>
  <div>
    <el-carousel :interval="5000" :height="`${height}px`" indicator-position="none">
      <el-carousel-item class="cover_image" v-for="item in imgUrl" :key="item" :style="{backgroundImage: 'url(' + item + ')' }">
      </el-carousel-item>
    </el-carousel>
    <shortcut></shortcut>
  </div>
</template>

<script>
import Shortcut from './Shortcut'

export default {
  components: {
    Shortcut
  },
  data () {
    return {
      imgUrl: [require('@/assets/1.jpg'), require('@/assets/2.jpg'), require('@/assets/3.jpg'), require('@/assets/4.jpg')],
      width: null,
      height: null,
      navHeight: 50,
      footerHeight: 0
    }
  },
  mounted () {
    this.width = document.documentElement.clientWidth
    this.height = document.documentElement.clientHeight - this.navHeight - this.footerHeight
    // this.recale = debounce(() => {
    //   this.width = document.documentElement.clientWidth
    //   this.height = document.documentElement.clientHeight
    // }, 100)
    let resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize'
    window.addEventListener(resizeEvt, this.recale, false)
  },
  methods: {
    recale () {
      this.width = document.documentElement.clientWidth
      this.height = document.documentElement.clientHeight - this.navHeight - this.footerHeight
    }
  }
}
</script>

<style scoped>
.el-carousel >>> .el-carousel__arrow {
  width: 50px;
  height: 50px;
  font-size: 18px;
}
.cover_image {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
</style>